* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'DM Serif Text', serif;
    font-family: 'Josefin Sans', sans-serif;
    font-family: 'Manrope', sans-serif;
    font-family: 'Poppins', sans-serif;
    font-family: 'Roboto', sans-serif;
}

.quote-box {
    width: 700px;
    background-color: #3D30A2;
    margin-block: 20px;
    padding: 20px;
    border-radius: 20px;
    color: white;
    border-left: 10px solid red;
    height: 150px;
    position: relative;
}

.quote-text {
    margin-bottom: 20px;
}

.quote-author {
    font-size: 0.7em;
}

.container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}

.btns {
    width: 600px;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.btns button {
    width: 140px;
    background-color: #279EFF;
    cursor: pointer;
    border: none;
    border-radius: 10px;
    padding: 10px;
    color: white;
    box-shadow: 1px 1px 1px 1px gray;
    transition: all .5s;
    font-weight: bold;
}

.btns button:hover {
    box-shadow: none;
}

.quoteNumber {
    background-color: red;
    font-weight: bold;
    font-weight: 3rem;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center
}

.progress-container {
    width: 600px;
    height: 8px;
    background-color: rgba(0, 0, 0, 0.2);
    margin: 20px auto;
    border-radius: 20px;
    overflow: hidden;
}

.progress {
    width: 40px;
    height: 100%;
    background-color: blue;
    transition: width .5s
}

.arrow {
    animation: animation-arrow 1s infinite ease-in forwards
}

@keyframes animation-arrow {
    0% {
        opacity: 1
    }

    100% {
        opacity: 0.5
    }
}

.favStar {
    position: absolute;
    cursor: pointer;
    display: block;
    margin-top: 3px;
    bottom: 10px;
    right: 10px
}